#instruction {
  max-width: 1920px;
  margin: 0 auto;
  padding: 90px 0 0;
  position: relative;
  overflow: hidden;
  background-color: #ffddbd;
}
#instruction .instruction_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (max-width: 1200px) {
  #instruction .instruction_content {
    padding: 90px 20px;
  }
}
#instruction .instruction_content .content_top h2 {
  font-size: 36px;
  line-height: 49px;
  color: #093b32;
  max-width: 360px;
  text-align: center;
  margin-bottom: 36px;
}
#instruction .instruction_content .content_bottom {
  display: flex;
  flex-wrap: wrap;
}
#instruction .instruction_content .content_bottom .single_block {
  flex-basis: 50%;
  display: flex;
  margin-bottom: 58px;
}
@media (max-width: 1000px) {
  #instruction .instruction_content .content_bottom .single_block {
    padding-right: 20px;
  }
}
@media (max-width: 800px) {
  #instruction .instruction_content .content_bottom .single_block {
    flex-basis: 100%;
    justify-content: center;
  }
}
@media (max-width: 500px) {
  #instruction .instruction_content .content_bottom .single_block {
    flex-direction: column;
    align-items: center;
    padding-right: unset;
  }
}
#instruction .instruction_content .content_bottom .single_block .left {
  margin-right: 20px;
}
@media (max-width: 500px) {
  #instruction .instruction_content .content_bottom .single_block .left {
    margin-right: unset;
    align-self: flex-start;
  }
  #instruction .instruction_content .content_bottom .single_block .left img {
    margin-bottom: 10px;
  }
}
#instruction .instruction_content .content_bottom .single_block .right {
  color: unset;
}
#instruction .instruction_content .content_bottom .single_block .right h3 {
  color: #093b32;
  font-family: "Prata", sans-serif;
  font-size: 24px;
  line-height: 33px;
  margin-bottom: 12px;
  max-width: 313px;
}
#instruction .instruction_content .content_bottom .single_block .right p {
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  line-height: 24px;
  max-width: 375px;
}