@charset "UTF-8";
#header {
  background-image: url("/image/rectangle-header-bg.png");
  max-width: 1920px;
  margin: 0 auto;
  height: 100vh;
  max-height: 843px;
  padding-top: 30px;
  position: relative;
  overflow: hidden;
  background-color: #f4f1e7;
}
@media (max-width: 1300px) {
  #header {
    padding: 30px 20px 0px;
  }
}
#header .section_wrapper:before {
  content: url("/image/header-before.png");
  width: 205px;
  height: 312px;
  position: absolute;
  left: 0px;
  top: 50px;
  filter: blur(6px);
  z-index: 30;
}
@media (max-width: 1650px) {
  #header .section_wrapper:before {
    display: none;
  }
}
#header .section_wrapper:after {
  content: "";
  width: 189px;
  height: 189px;
  position: absolute;
  right: 100px;
  bottom: 50px;
  background-color: #ffddbd;
  filter: blur(120px);
  z-index: 30;
}
@media (max-width: 1650px) {
  #header .section_wrapper:after {
    display: none;
  }
}
#header:after {
  content: "";
  position: absolute;
  background-image: url("/image/rectangle-header-zalivka.png");
  z-index: 11;
  bottom: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
}
#header:before {
  content: "";
  background: url("/image/header-line.png");
  position: absolute;
  z-index: 12;
  bottom: 15px;
  left: 0px;
  width: 100%;
  height: 12px;
}
#header .header_top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 90px;
  z-index: 20;
}
@media (max-width: 850px) {
  #header .header_top {
    align-items: flex-start;
  }
}
@media (max-width: 650px) {
  #header .header_top {
    margin-bottom: 25px;
  }
}
#header .header_top nav {
  display: flex;
  justify-content: space-between;
  max-width: 742px;
  width: 100%;
  z-index: 20;
}
@media (max-width: 1050px) {
  #header .header_top nav {
    max-width: 600px;
  }
}
@media (max-width: 850px) {
  #header .header_top nav.open_menu {
    display: block;
    position: absolute;
    background-color: #072e27;
    padding: 20px;
    height: 100vh;
    z-index: 100;
    box-sizing: border-box;
    top: 0;
    left: 0;
    max-width: 100%;
  }
  #header .header_top nav a {
    display: block;
    margin-bottom: 20px;
    font-size: 20px;
  }
}
#header .header_top nav a {
  color: #f2f2f2;
  text-decoration: none;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}
@media (max-width: 850px) {
  #header .header_top nav {
    display: none;
  }
}
#header .header_top .header_logo {
  z-index: 20;
}
@media (max-width: 850px) {
  #header .header_menu-burger {
    position: relative;
    /* задаем относительное позиционирование */
    width: 40px;
    /* определяем размеры блока */
    height: 35px;
    display: block;
    z-index: 200;
  }
  #header .header_menu-burger span,
#header .header_menu-burger:after,
#header .header_menu-burger:before {
    height: 3px;
    width: 100%;
    position: absolute;
    background: #fff;
    margin: 0 auto;
  }
  #header .header_menu-burger span {
    top: 16px;
  }
  #header .header_menu-burger:after,
#header .header_menu-burger:before {
    content: "";
  }
  #header .header_menu-burger:after {
    bottom: 5px;
  }
  #header .header_menu-burger:before {
    top: 5px;
  }
  #header .header_menu-burger.open_menu span {
    opacity: 0;
    transition: 0.5s;
  }
  #header .header_menu-burger.open_menu:before {
    transform: rotate(38deg);
    top: 16px;
    transition: 0.4s;
  }
  #header .header_menu-burger.open_menu:after {
    transform: rotate(-38deg);
    bottom: 16px;
    transition: 0.4s;
  }
}
#header .header_content {
  display: flex;
  justify-content: space-between;
}
@media (max-width: 650px) {
  #header .header_content {
    flex-wrap: wrap;
  }
}
#header .header_content .content_left {
  width: 514px;
  z-index: 20;
}
@media (max-width: 650px) {
  #header .header_content .content_left {
    width: 100%;
    order: 2;
  }
}
#header .header_content .content_left h1 {
  color: #f2f2f2;
  font-family: "Prata", sans-serif;
  font-size: 56px;
  line-height: 76px;
  font-weight: normal;
  margin-bottom: 38px;
}
@media (max-width: 1050px) {
  #header .header_content .content_left h1 {
    font-size: 44px;
  }
}
@media (max-width: 650px) {
  #header .header_content .content_left h1 {
    text-align: center;
    font-size: 30px;
    line-height: 46px;
  }
}
@media (max-width: 450px) {
  #header .header_content .content_left h1 {
    font-size: 28px;
    line-height: 38px;
    margin-bottom: 20px;
  }
}
#header .header_content .content_left h1 span:nth-child(1) {
  text-transform: uppercase;
}
#header .header_content .content_left h1 span:nth-child(2) {
  color: #ffddbd;
}
#header .header_content .content_left p {
  color: #f2f2f2;
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  line-height: 29px;
  max-width: 388px;
  font-weight: 300;
  margin-bottom: 50px;
  position: relative;
  padding-left: 24px;
}
@media (max-width: 1050px) {
  #header .header_content .content_left p {
    font-size: 20px;
  }
}
@media (max-width: 650px) {
  #header .header_content .content_left p {
    max-width: 100%;
    text-align: center;
  }
}
@media (max-width: 450px) {
  #header .header_content .content_left p {
    margin-bottom: 25px;
    font-size: 16px;
    line-height: 28px;
    text-align: unset;
  }
}
#header .header_content .content_left p:before {
  position: absolute;
  content: url("/image/header-eclipse.png");
  top: 0;
  left: 0;
  width: 12px;
  height: 12px;
  z-index: 20;
}
#header .header_content .content_left button {
  width: 420px;
  height: 70px;
  background: linear-gradient(180deg, #fee0d6 0%, #ffb8a3 49.27%, #ffaf95 100%);
  border-radius: 87px;
  color: #f2f2f2;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  display: block;
  cursor: pointer;
}
@media (max-width: 900px) {
  #header .header_content .content_left button {
    width: 320px;
    margin: 0 auto;
  }
}
#header .header_content .content_right {
  z-index: 20;
}
@media (max-width: 650px) {
  #header .header_content .content_right {
    width: 100%;
    order: 1;
  }
}
#header .header_content .content_right img {
  display: block;
  margin: 0 auto 20px;
}
@media (max-width: 850px) {
  #header .header_content .content_right img {
    max-width: 80%;
  }
}
@media (max-width: 650px) {
  #header .header_content .content_right img {
    max-width: 50%;
  }
}
@media (max-width: 500px) {
  #header .header_content .content_right img {
    max-width: 65%;
  }
}
@media (max-width: 450px) {
  #header .header_content .content_right img {
    max-width: 65%;
  }
}