#questions {
  max-width: 1920px;
  margin: 0 auto;
  padding: 90px 0 0;
  position: relative;
  overflow: hidden;
  background-color: #f4f1e7;
}
#questions .questions_content {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-bottom: 50px;
}
@media (max-width: 1000px) {
  #questions .questions_content {
    padding: 0 20px 60px;
  }
}
#questions .questions_content .content_top h2 {
  font-size: 36px;
  line-height: 130%;
  margin-bottom: 32px;
  font-family: "Prata", sans-serif;
}
#questions .questions_content .content_bottom {
  padding: 20px;
  border: 1px solid #093b32;
}
#questions .questions_content .content_bottom .question_container label {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
  z-index: 10;
  transition: color 0.35s;
  height: 62px;
}
#questions .questions_content .content_bottom .question_container label span {
  font-size: 20px;
  font-weight: 500;
  line-height: 130%;
  margin-bottom: 29px;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 500px) {
  #questions .questions_content .content_bottom .question_container label span {
    font-size: 16px;
    line-height: 130%;
    width: calc(100% - 40px);
  }
}
#questions .questions_content .content_bottom .question_container label:after {
  content: url("/image/question-close.png");
  float: right;
}
@media (max-width: 500px) {
  #questions .questions_content .content_bottom .question_container label:after {
    margin-bottom: 30px;
  }
}
#questions .questions_content .content_bottom .question_container .acor_body {
  height: 0;
  transition: color 0.5s, padding 0.5s;
  overflow: hidden;
}
#questions .questions_content .content_bottom .question_container .acor_body p {
  font-size: 18px;
  line-height: 22px;
  font-family: "Montserrat", sans-serif;
  font-weight: 300;
}
#questions .questions_content .content_bottom .question_container input {
  display: none;
}
#questions .questions_content .content_bottom .question_container input:checked + label:after {
  transition: transform 0.15s;
  transform: rotate(45deg);
}
#questions .questions_content .content_bottom .question_container input:checked + label + .acor_body {
  height: auto;
  margin-top: -5px;
}
@media (max-width: 1000px) {
  #questions .questions_content .content_bottom .question_container input:checked + label + .acor_body {
    width: calc(100% - 40px);
  }
}
#questions .questions_content .content_bottom .question_container hr {
  margin: 20px 0 30px 0;
  color: #093b32;
  border-bottom: 1px solid #093b32;
}