#about {
  background-color: #f4f1e7;
  max-width: 1920px;
  margin: 0 auto;
  padding: 105px 0;
  position: relative;
}
@media (max-width: 1000px) {
  #about {
    padding: 105px 20px;
  }
}
#about .section_wrapper:after {
  position: absolute;
  content: url("/image/about-yoga-after.png");
  width: 175px;
  height: 194px;
  top: 50px;
  right: 0px;
}
@media (max-width: 1200px) {
  #about .section_wrapper:after {
    display: none;
  }
}
#about .about_content {
  display: flex;
}
@media (max-width: 800px) {
  #about .about_content {
    flex-direction: column;
    align-items: center;
  }
}
#about .about_content .content_left {
  margin-right: 100px;
}
@media (max-width: 800px) {
  #about .about_content .content_left {
    margin-bottom: 20px;
    margin-right: unset;
  }
}
@media (max-width: 650px) {
  #about .about_content .content_left img {
    width: 70%;
    margin: 0 auto;
    display: block;
  }
}
#about .about_content .content_rigth {
  max-width: 610px;
}
@media (max-width: 1000px) {
  #about .about_content .content_rigth {
    max-width: 100%;
  }
}
#about .about_content .content_rigth h2 {
  font-family: "Prata", sans-serif;
  font-size: 36px;
  line-height: 49px;
  color: #093b32;
  margin-bottom: 20px;
}
@media (max-width: 1000px) {
  #about .about_content .content_rigth h2 {
    font-size: 30px;
    line-height: 40px;
  }
}
@media (max-width: 500px) {
  #about .about_content .content_rigth h2 {
    font-size: 22px;
    line-height: 30px;
  }
}
#about .about_content .content_rigth p {
  font-family: "Montserrat", sans-serif;
  font-size: 24px;
  line-height: 29px;
  font-weight: 300;
}
@media (max-width: 1000px) {
  #about .about_content .content_rigth p {
    font-size: 20px;
    line-height: 25px;
  }
}
@media (max-width: 500px) {
  #about .about_content .content_rigth p {
    font-size: 16px;
    line-height: 20px;
  }
}