#reviews {
  background-color: #f2f2f2;
  padding: 120px 0;
  margin: 0 auto;
  max-width: 1920px;
}
#reviews .reviews_content {
  justify-content: center;
}
@media (max-width: 1200px) {
  #reviews .reviews_content {
    padding: 0 20px;
  }
}
#reviews .reviews_content .content_top h2 {
  font-size: 36px;
  line-height: 49px;
  font-family: "Prata", sans-serif;
  color: #093b32;
  text-align: center;
  margin-bottom: 36px;
}
#reviews .reviews_content .content_bottom {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom {
    flex-direction: column;
  }
}
#reviews .reviews_content .content_bottom .left_button {
  width: 41px;
  height: 41px;
  transform: rotate(45deg);
  border-left: 6px solid #1e7c7f;
  border-bottom: 6px solid #1e7c7f;
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom .left_button {
    order: 2;
    align-self: flex-end;
    right: 80px;
    position: relative;
    top: 20px;
    width: 60px;
    height: 60px;
    border-width: 10px;
  }
}
#reviews .reviews_content .content_bottom .right_button {
  width: 41px;
  height: 41px;
  transform: rotate(45deg);
  border-right: 6px solid #1e7c7f;
  border-top: 6px solid #1e7c7f;
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom .right_button {
    order: 3;
    align-self: flex-end;
    position: relative;
    bottom: 40px;
    right: 10px;
    width: 60px;
    height: 60px;
    border-width: 10px;
  }
}
#reviews .reviews_content .content_bottom .center {
  flex-basis: 1070px;
  padding: 40px 80px 40px 40px;
  min-height: 342px;
  background: url("/image/reviews-bg.png") no-repeat;
  background-size: contain;
}
@media (max-width: 1110px) {
  #reviews .reviews_content .content_bottom .center {
    background: url("/image/reviews-zalivka.png") repeat;
  }
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom .center {
    order: 1;
    margin-bottom: 20px;
    flex-basis: unset;
    padding: 30px;
  }
}
#reviews .reviews_content .content_bottom .center .single_review {
  display: none;
}
#reviews .reviews_content .content_bottom .center .single_review.active {
  display: block;
}
#reviews .reviews_content .content_bottom .center .single_review h3 {
  font-size: 25px;
  line-height: 35px;
  font-family: "Montserrat", sans-serif;
  color: #ffddbd;
  margin-bottom: 15px;
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom .center .single_review h3 {
    font-size: 20px;
    line-height: 24px;
    font-weight: 500;
    margin-bottom: 15px;
  }
}
#reviews .reviews_content .content_bottom .center .single_review p {
  font-size: 22px;
  line-height: 27px;
  font-family: "Montserrat", sans-serif;
  color: #f2f2f2;
  margin-bottom: 24px;
  font-weight: 300;
}
@media (max-width: 800px) {
  #reviews .reviews_content .content_bottom .center .single_review p {
    font-size: 16px;
    line-height: 20px;
  }
}
#reviews .reviews_content .content_bottom .center .single_review p.name {
  font-size: 22px;
  font-weight: 500;
  font-family: "Montserrat", sans-serif;
  color: #f4f1e7;
}