#footer {
  flex: 0 0 auto;
  background-color: #093b32;
  max-width: 1920px;
  margin: 0 auto;
  font-weight: normal;
}
#footer .footer_content {
  display: flex;
  justify-content: space-between;
  color: #fff;
  font-size: 14px;
  line-height: 130%;
  font-weight: normal;
  padding: 93px;
  font-family: "Montserrat", sans-serif;
}
@media (max-width: 1000px) {
  #footer .footer_content {
    padding: 50px;
  }
}
@media (max-width: 900px) {
  #footer .footer_content {
    padding: 50px 20px;
  }
}
@media (max-width: 800px) {
  #footer .footer_content {
    flex-direction: column;
    align-items: center;
    font-size: 16px;
    line-height: 150%;
  }
}
#footer .footer_content .left ol li {
  margin-bottom: 15px;
}
#footer .footer_content .left ol li:nth-child(3) {
  margin-bottom: 50px;
}
#footer .footer_content .middle {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#footer .footer_content .middle p {
  margin-bottom: 20px;
}
#footer .footer_content .middle div {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
}
#footer .footer_content .middle div img {
  margin-right: 10px;
}
#footer .footer_content .middle div:nth-child(2) {
  margin-bottom: 40px;
}
#footer .footer_content .right li {
  margin-bottom: 25px;
}
#footer .footer_content .right a {
  color: #fff;
  text-decoration: none;
}