#programm {
  padding: 120px 0;
  margin: 0 auto;
  max-width: 1920px;
  background-color: #f2f2f2;
  position: relative;
}
#programm .section_wrapper {
  max-width: 960px;
}
#programm .section_wrapper:before {
  content: url("/image/programm-before.png");
  position: absolute;
  width: 97px;
  height: 292px;
  top: -50px;
  left: 30px;
}
@media (max-width: 1500px) {
  #programm .section_wrapper:before {
    display: none;
  }
}
#programm .section_wrapper:after {
  content: url("/image/programm-after.png");
  position: absolute;
  width: 141px;
  height: 273px;
  bottom: 40px;
  right: 150px;
}
@media (max-width: 1500px) {
  #programm .section_wrapper:after {
    display: none;
  }
}
#programm .programm_content h2 {
  text-align: center;
  font-size: 36px;
  line-height: 49px;
  font-family: "Prata", sans-serif;
  margin-bottom: 40px;
}
#programm .programm_content .programm_list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
#programm .programm_content .programm_list .single_block {
  flex-basis: 33.3%;
}
@media (max-width: 750px) {
  #programm .programm_content .programm_list .single_block {
    flex-basis: 50%;
  }
}
@media (max-width: 500px) {
  #programm .programm_content .programm_list .single_block {
    flex-basis: 100%;
  }
}
#programm .programm_content .programm_list .single_block img {
  display: block;
  margin: 0 auto;
  margin-bottom: 15px;
}
@media (max-width: 500px) {
  #programm .programm_content .programm_list .single_block img {
    display: block;
    width: 40%;
  }
}
#programm .programm_content .programm_list .single_block p {
  max-width: 240px;
  text-align: center;
  margin: 0 auto;
  font-family: "Montserrat", sans-serif;
  font-size: 20px;
  line-height: 24px;
}
#programm .programm_content .programm_list .single_block:nth-child(1) {
  margin-bottom: 100px;
}
@media (max-width: 750px) {
  #programm .programm_content .programm_list .single_block:nth-child(2),
#programm .programm_content .programm_list .single_block:nth-child(3),
#programm .programm_content .programm_list .single_block:nth-child(4) {
    margin-bottom: 100px;
  }
}
@media (max-width: 500px) {
  #programm .programm_content .programm_list .single_block {
    margin-bottom: 40px !important;
  }
}