#complect {
  max-width: 1920px;
  margin: 0 auto;
  padding: 120px 0;
  position: relative;
  overflow: hidden;
  background-color: #f2f2f2;
}
#complect .complect_content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
#complect .complect_content .content_top h2 {
  font-size: 36px;
  line-height: 49px;
  font-family: "Prata", sans-serif;
  max-width: 525px;
  text-align: center;
  margin-bottom: 36px;
}
#complect .complect_content .content_bottom {
  display: flex;
}
@media (max-width: 1200px) {
  #complect .complect_content .content_bottom {
    background: url("/image/complect-slider-bg.png") no-repeat center;
    background-size: cover;
    max-width: 500px;
    width: 100%;
    max-height: 740px;
    height: 740px;
    display: block;
    overflow: hidden;
    position: relative;
    padding: 46px 17px;
  }
}
@media (max-width: 500px) {
  #complect .complect_content .content_bottom {
    width: 95% !important;
  }
}
#complect .complect_content .content_bottom .single_block {
  flex: 414px 1 1;
  align-items: center;
  justify-content: space-between;
  padding: 40px;
}
@media (max-width: 1200px) {
  #complect .complect_content .content_bottom .single_block {
    background: unset !important;
    display: none;
    width: 500px !important;
    position: absolute;
    opacity: 1;
    transition: opacity 1s ease-in;
    width: 100%;
    transform: translateX(-50%);
    left: 50%;
  }
  #complect .complect_content .content_bottom .single_block.active {
    display: block;
    text-align: center;
  }
  #complect .complect_content .content_bottom .single_block.active img {
    width: 60%;
    margin-bottom: 20px;
  }
  #complect .complect_content .content_bottom .single_block.active h3 {
    font-size: 36px;
    line-height: 40px;
  }
  #complect .complect_content .content_bottom .single_block.active p {
    font-size: 24px;
    line-height: 36px;
  }
}
@media (max-width: 500px) {
  #complect .complect_content .content_bottom .single_block {
    width: 100% !important;
    padding: 40px 10px !important;
  }
  #complect .complect_content .content_bottom .single_block h3 {
    width: 100% !important;
  }
  #complect .complect_content .content_bottom .single_block p {
    width: 100% !important;
  }
}
#complect .complect_content .content_bottom .single_block img {
  display: block;
  margin: 0 auto;
  margin-bottom: 20px;
}
#complect .complect_content .content_bottom .single_block h3 {
  font-size: 24px;
  line-height: 33px;
  font-family: "Prata", sans-serif;
  color: #ffddbd;
  text-align: center;
  max-width: 270px;
  margin: 0 auto;
  margin-bottom: 20px;
}
#complect .complect_content .content_bottom .single_block p {
  font-family: "Montserrat", sans-serif;
  color: #f2f2f2;
  font-size: 20px;
  line-height: 24px;
  text-align: center;
}
#complect .complect_content .content_bottom .single_block:nth-child(1) {
  background: url("/image/complect-bg1.png") no-repeat center;
  background-size: cover;
  width: 414px;
  height: 414px;
}
#complect .complect_content .content_bottom .single_block:nth-child(2) {
  background: url("/image/complect-bg2.png") no-repeat center;
  background-size: cover;
  width: 414px;
  height: 414px;
}
#complect .complect_content .content_bottom .single_block:nth-child(3) {
  background: url("/image/complect-bg3.png") no-repeat center;
  background-size: cover;
  width: 414px;
  height: 414px;
}
#complect .complect_content .content_bottom .complect_radioGroup {
  position: absolute;
  display: flex;
  justify-content: space-between;
  width: 123px;
  bottom: 20px;
  left: 50%;
  transform: translateX(-50%);
}
#complect .complect_content .content_bottom .complect_radioGroup .radio_container {
  position: relative;
}
#complect .complect_content .content_bottom .complect_radioGroup .radio_container input {
  display: none;
}
#complect .complect_content .content_bottom .complect_radioGroup .radio_container label {
  width: 25px;
  height: 25px;
  display: inline-block;
  border: 0.5px solid #fff1e3;
  border-radius: 50%;
  opacity: 0.5;
}
#complect .complect_content .content_bottom .complect_radioGroup .radio_container label:before {
  position: absolute;
  top: 45%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  content: "";
  width: 7px;
  height: 7px;
  background-color: #ffddbd;
  border-radius: 50%;
  opacity: 0.5;
}
#complect .complect_content .content_bottom .complect_radioGroup .radio_container input:checked + label {
  opacity: 1;
}